import React, {Component} from 'react';
import Slider from 'react-slick';
import {connect} from 'react-redux';
import { bindActionCreators } from 'redux';
import * as blocksActions from '../../actions/blocksActions';

class LeftNavButton extends Component {
    render() {
        return <div onClick={this.props.onClick} className="prevArrow">{'<'}</div>
    }
}
class RightNavButton extends Component {
    render() {
        return <div onClick={this.props.onClick} className="prevArrow">{'>'}</div>
    }
}

class Blocks extends Component{
    constructor(props){
        super(props);

        const { initBlocks } = props.blocksActions;
        let arrayBlocks = [];
        for(let i = 0;i<25;i++){
            arrayBlocks[i] = i*10;
        }
        initBlocks(arrayBlocks);

        this.state = {

        };

    }



    render(){

        let settings = {
            dots: false,
            infinite: true,
            draggable: false,
            focusOnSelect: true,
            speed: 500,
            slidesToShow: 10,
            slidesToScroll: 2,
            variableWidth: true,
            className: 'blocksCarousel',
            prevArrow:<LeftNavButton/>,
            nextArrow: <RightNavButton/>,
        };

        let sliderContent = [];
        console.log('1111');
        this.props.blocks.blocks.map( (item,i)=>{
            console.log('1');
            sliderContent.push(<div className="block" key={i}>{+item}</div>);
        });
        console.log(sliderContent);
        return(
            <div className="blocks">
                <div className="blocksContainer">
                    <Slider {...settings} style="display: flex;">
                        {sliderContent}
                    </Slider>
                </div>
            </div>
        );
    }
}

function mapStateToProps (state) {
    return {
        blocks: state.blocksReducer,
    }
}

function mapDispatchToProps(dispatch) {
    return{
        blocksActions: bindActionCreators(blocksActions, dispatch)
    };
}


export default connect(mapStateToProps, mapDispatchToProps)(Blocks);